@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
body {
    background-image: linear-gradient(to right bottom,#00F260, #0575E6);
    background-attachment: fixed;
}
a {
    text-decoration: none;
    color: #354548;
}
a:hover {
    text-decoration: none;
    color: #000;
}
.app__main-content {
    background-color: #fff;
    border-radius: 20px;
    padding: 25px 15px;
    box-shadow: 0 0 10px black;
}

.row {
    height: 90vh;
}
.container {
    padding-top: 50px;
}

/*sidebar */
.sidebar {
   background-color: #fff;
   height: 100%;
   border-radius: 20px;
   padding: 25px 15px;
   box-shadow: 0 0 10px black;
   text-align: center;
}

.sidebar__avatar {
    height: 110px;
    width: 120px;
    object-fit: contain;
    border-radius: 30px;
}
.sidebar__icon {
    width: 25px;
    height: 25px;
}
.sidebar__name{
    font-family: 'Kaushan script';
    margin: 20px 0;
    font-size: 1.7rem;
}
.sidebar__name>span {
    color: #00F260;
}
.sidebar__item {
    margin-bottom: 6px;
    padding-top: 6px;
    font-size: 1.05rem;
    height: 40px;
    border-radius: 30px;
}
.sidebar__title {
    background-color: #f1f1f1;
    border-radius: 30px;
}
.sidebar__contact {
    background-color: #dbdddf;
    padding: 2px 0;
    margin: 20px -15px;
}
.sidebar__email {
    background-image: linear-gradient(to right bottom,#00F260, #0575E6);
    border-radius: 30px;
    color: #fff;
    margin: 15px 25px 0px;
    cursor: pointer;
    transition: all 3s linear;
}
.sidebar__email:hover{
    transform: scale(1.2);
}
.sidebar__github:hover,
.sidebar__resume:hover {
    background-color: #f1f1f2;
    color: #000;
}
@media (max-width:991px){
    .app__main-content{
        margin: 30px 0;
    }
}

/* Navbar */

.navbar__items {
    display: flex;
}
.navbar__active {
    font-size: 2rem;
    font-weight: 700;
    color: #354358;
    border-bottom: 4px solid #00f260;
}
.navbar__item {
    margin-right: 20px;
    text-decoration: none;
    color: #354358;
    font-size: 1.3rem;
    cursor: pointer;
}
.navbar__item:hover{
    background:  linear-gradient(#00F260, #0575E6);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
